"use client";

import { Textarea } from "~/components/ui/textarea";
import { Switch } from "~/components/ui/switch";
import { useState, useEffect } from "react";

export default function Home() {
  const [isDark, setIsDark] = useState(true);
  const [text, setText] = useState("");
  const [response, setResponse] = useState("");

  // Manejar el tema inicial y los cambios
  useEffect(() => {
    // Establecer tema oscuro por defecto
    document.documentElement.classList.add("dark");
  }, []);

  const toggleTheme = () => {
    setIsDark(!isDark);
    document.documentElement.classList.toggle("dark");
  };

  const handleSubmit = async () => {
    try {
      const res = await fetch("/api/summary", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ text }),
      });

      const data = await res.json();
      setResponse(data.message);
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <main className="min-h-screen bg-white dark:bg-slate-950 transition-colors duration-300 flex justify-center items-center">
      {/* Switch de tema en la esquina superior derecha */}
      <div className="absolute right-8 top-8 flex items-center gap-2">
        <Switch checked={isDark} onCheckedChange={toggleTheme} />
        <span className="text-sm text-slate-600 dark:text-slate-400">
          {isDark ? "Modo oscuro" : "Modo claro"}
        </span>
      </div>

      <div className="flex flex-col items-center justify-center p-8 w-full max-w-4xl mx-auto">
        {/* Título elegante usando las clases de tipografía de shadcn */}
        <div className="mb-12 space-y-2 text-center">
          <h1 className="text-4xl font-bold tracking-tighter sm:text-5xl md:text-6xl lg:text-7xl">
            Resumenes
          </h1>
          <p className="mx-auto max-w-[700px] text-gray-500 md:text-xl dark:text-gray-400">
            Escribe tus ideas y resúmenes aquí
          </p>
        </div>

        {/* Contenedor del textarea */}
        <div className="w-full max-w-3xl space-y-4">
          <Textarea
            placeholder="Escribe tu resumen aquí..."
            className="min-h-[300px] p-4 text-lg"
            value={text}
            onChange={(e) => setText(e.target.value)}
          />
          <button
            onClick={handleSubmit}
            className="w-full bg-slate-900 text-white dark:bg-slate-100 dark:text-slate-900 py-2 rounded-md hover:opacity-90 transition-opacity"
          >
            Enviar
          </button>
          {response && (
            <p className="text-center text-slate-600 dark:text-slate-400">
              Respuesta: {response}
            </p>
          )}
        </div>
      </div>
    </main>
  );
}
